import React from 'react';
import { Menu } from 'antd';
import './index.css';
import {GITHUB_URL,tabKeyRouterMap} from '../../constants'
import { useHistory,useLocation} from 'react-router-dom';
import {
    HomeOutlined,
    QuestionCircleOutlined,
    GithubOutlined
  } from '@ant-design/icons';

const {useEffect,useState} = React;


function Nav() {
    const history = useHistory();
    const location = useLocation();
    // const defaultMenu = [tabKeyRouterMap[location.pathname]];
    const [selectedKeys,setSelectedKeys] = useState([]);

    useEffect(()=>{
        setSelectedKeys([tabKeyRouterMap[location.pathname]])
    },[location]);     

    const gotoGithub = ()=>{
        window.location.href = GITHUB_URL;
    }
    const handleMenuClick = ({key})=>{
        history.push(tabKeyRouterMap[key]);
    }
    return (
        <div className="nav-header">
            {/* <div className="logo"></div> */}
            <div className="nav-header-item">
                <GithubOutlined type="github" onClick ={gotoGithub}/>
            </div>
            <Menu mode="horizontal" theme="light"  selectedKeys ={selectedKeys} style={{ lineHeight: '63px',border:0  }} onClick={handleMenuClick}>
                <Menu.Item key="home"><HomeOutlined />首页</Menu.Item>
                <Menu.Item key="help"><QuestionCircleOutlined />关于</Menu.Item>
            </Menu>

        </div>

    )

}
export default Nav;